import { useEffect } from "react";
import { change } from "redux-form";
import { useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "redux/hooks";

export default function AddProductForm({ id }: any) {
  const form = "AddProductForm";
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const product = useAppSelector((state) => state.product.product);

  useEffect(() => {
    if (!product || product?.data === "Not Found") return;
    const {
      isPublished,
      preventUnder16,
      preventUnder18,
      SKU,
      barcode,
      gtin,
      categories,
      maximumBuyableCount,
      criticalStock,
      stockType,
      purchasedPrice,
      oldPrice,
      newPrice,
      taxPercentage,
      displayOrder,
      translations,
    } = product;
    dispatch(change(form, "isPublished", isPublished));
    dispatch(change(form, "preventUnder16", preventUnder16));
    dispatch(change(form, "preventUnder18", preventUnder18));
    dispatch(change(form, "SKU", SKU));
    dispatch(change(form, "barcode", barcode));
    dispatch(change(form, "gtin", gtin));
    dispatch(change(form, "categories", categories));
    dispatch(change(form, "maximumBuyableCount", maximumBuyableCount));
    dispatch(change(form, "criticalStock", criticalStock));
    dispatch(change(form, "stockType", stockType));
    dispatch(change(form, "purchasedPrice", purchasedPrice));
    dispatch(change(form, "oldPrice", oldPrice));
    dispatch(change(form, "newPrice", newPrice));
    dispatch(change(form, "taxPercentage", taxPercentage));
    dispatch(change(form, "displayOrder", displayOrder));
    dispatch(
      change(
        form,
        "translations",
        translations.map(({ _id, ...rest }: any) => ({ ...rest }))
      )
    );
  }, [product, navigate, dispatch]);

  return null;
}
