import { Container } from "@mui/material";
import GoBack from "components/atoms/GoBack";
import AddProductForm from "./AddProductForm";
import Banner from "components/templates/Banner";
import BasicCard from "components/templates/BasicCard";

import ProductsService from "services/products.service";
import { useNavigate, useParams } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import CircleLoader from "components/atoms/CircleLoader";
import UpdateProductForm from "./UpdateProductForm";

export default function AddProduct() {
  const { id } = useParams();
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const loading = useAppSelector((state) => state.formLoader.loading);

  const handleSubmit = (values: any) => {
    let data = { ...values };

    data.categories = data.categories.map(({ _id, ...rest }: any) => ({
      ...rest,
    }));

    if (id) ProductsService.updateProduct(`${id}`, data, dispatch);
    else ProductsService.addProduct(data, dispatch, navigate);
  };

  return (
    <>
      {id ? (
        <BasicCard>
          {loading && <CircleLoader />}
          <AddProductForm onSubmit={handleSubmit} />
          <UpdateProductForm id={id} />
        </BasicCard>
      ) : (
        <Container maxWidth="lg">
          <GoBack path="/product-management" title="Products" />
          <Banner heading="Add New Product"></Banner>
          <BasicCard>
            {loading && <CircleLoader />}
            <AddProductForm onSubmit={handleSubmit} />
          </BasicCard>
        </Container>
      )}
    </>
  );
}
